.video-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: -1;
  background: $color-main;
  opacity: 1;
  .mobile & {
    background: img('image.jpg') left / cover no-repeat;
    opacity: 0.3;
  }
}

.video_background,
iframe,
.wistia_responsive_padding {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  //height: auto;
  //width: auto;
  display: none;
  opacity: 0.5;
  @include from(l) {
    display: block;
  }
}
