@function img($img) {
  @return url(#{$images-path}/#{$img});
}

%list-style{
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
@function texto3d($numero,$color){
  $text-shadow: 2px 2px $color;
  @for $i from 1 through $numero {
    $text-shadow: '#{$text-shadow},
    #{$i}px #{$i}px #{$color}';
  }
  @return unquote($text-shadow);
}
