// Typography
$font-family: 'Montserrat', sans-serif;
$color-main: #0a38a1;
$color-font: #dfdfdf;
$color-second: #d87539;
$body-bg: $color-main;
$font-color: #fdfdfd;
$color-text: #fdfdfd;


$width100: calc(100vw - var(--scrollbar));

:root {
  --body-bg: #{$body-bg};
  --font-color: #{$font-color};
}

:root {
  --header-height: 3rem;

  @include from(l) {
    --header-height: 4rem;
  }
}

// z-ndex
$z-back: -10;
$z-normal: 1;
$z-tooltip: 10;
$z-fixed: 100;
$z-modal: 1000;

:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

$images-path: "../img";

:root {
  --color-main: #{$color-main};
  --color-font: #{$color-font};
  --color-second: #{$color-second};
}
