.modal{
  $height: 3rem;
  --height: #{$height};
  position: fixed;
  z-index: var(--z-modal);
  display: flex;
  width: 100vw;
  height: 100vh;
  background: rgba(#2c3940,.75) ;

  &__container{
    display: table;
    width: 80%;
    height: auto;
    max-width: 400px;
    margin: 0 auto;
    //background: #fefefe;
    background: rgba(darken(#2c3940,10),.85);
    padding: calc(var(--height) * .75);
    border-radius: 0 0 1em 1em;
    color: #fdfdfd;
    .mobile &{
      @media screen and(orientation: landscape){
        transform-origin: center top;
        transform: scale(.7);
        max-width: 600px;
      }
    }
  }
  &__title{
    text-align: center;
    text-transform: uppercase;
  }
  &__form{
    margin: 0;
  }
}
.input-group{
  position: relative;
  height: var(--height);
  margin-bottom: .5em;
  overflow: hidden;
  //border-radius: 0 0 .3rem .3rem;
  border-bottom: 1px solid #fff;
  label, input, textarea{
    display: block;
    height: calc(var(--height)*.5) t;
    line-height: calc(var(--height)*.5);
    width: 100%;
    max-width: 100%;
    padding: 0;
    font-family: $font-family;
    color: #dfdfdf;
    //font-size: .5em;
  }
  label{
    position: absolute;
    bottom: 0;
    padding-left: .5em;
    color: $color-main;
    z-index: 10;
    transition: all .3s;
    transform: translateY(0);
    color: #fdfdfd;
  }
  input, textarea{
    border: none;
    font-size: 1em;
    position: relative;
    top: 50%;
    background: transparent;
    padding: 0 .5em;
    &:-webkit-autofill{
      background: transparent !important;
    }
    &:focus{
      box-shadow: none;
      outline: none;
      & + label{
        color: #fdfdfd;
        transform: translateY(-100%);
        font-size: .8em;
      }
    }
  }
  &.message{
    textarea{
      height: calc(var(--height) * .5);
      resize: none;
    }
  }
}
input:-webkit-autofill {
  background-color: blue !important;
}
