*,
*::before,
*:after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  margin: 0;
}

body {
  font-family: $font-family;
  background: #000;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-size: 16px;
  color: $color-text;
}

a {
  color: inherit;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img,
picture,
picture img {
  display: block;
  max-width: 100%;
  height: auto;
}

section,
figure,
article {
  margin: 0;
  padding: 0;
}
p {
  margin-bottom: 1em;
  &:last-child,
  &:only-child,
  &:last-of-type,
  &:only-of-type {
    margin-bottom: 0;
  }
}
