}

body.dev:before,
body.desarrollo:before {
  content: attr(class);
  font-style: italic;
  font-size: 0.75rem;
  font-weight: normal;
  z-index: 1;
  right: 0;
  position: absolute;
  display: table;
  width: 100%;
  padding: 0 0.3125em;
  height: 1.25rem;
  line-height: 1.25rem;
}

body.dev,
body.desarrollo {
  margin: 0 !important;
  position: relative;
  top: 66px;
}

body.dev:before,
body.desarrollo:before {
  position: fixed;
  background: steelblue;
  color: #FFF;
  font-size: 0.875rem;
  text-align: center;
  line-height: 36px;
  height: 36px;
  top: 0;
}

body.dev:before,
body.desarrollo:before {
  content: "ed-grid: size s, from 0 (ed-containers: red, ed-items: blue, yellow)";
}

@media screen and (min-width: 640px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size m, from 640px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1024px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size l, from 1024px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1024px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size lg, from 1024px (ed-containers: red, ed-items: blue, yellow)";
  }
}

@media screen and (min-width: 1440px) {
  body.dev:before,
  body.desarrollo:before {
    content: "ed-grid: size xl, from 1440px (ed-containers: red, ed-items: blue, yellow)";
  }
}

