.container{
  width: 80%;
  max-width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  min-height: 150px;
  //background: $color-main;
  transform: translate(-50%, -50%);
  .mobile &{
    @include from(l){
      //opacity: 0;
      max-width: 70%;
    }
  }
}
.logo-container{
  $mbottom: 5em;
  margin-bottom: $mbottom;
  position: relative;
  &:after{
    content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translateY($mbottom/2);
      height: 2px;
      width: 100%;
      background-color: $color-font;
      border-radius: 5em;
  }
  img{
    width: 100%;
    height: auto;
  }
}
.data{
  color: $color-font;
  text-align: center;
  .text{
    font-size: 1.4em;
    line-height: 1.2;
    margin-bottom: .3em;
    &:last-of-type, &:last-child{
      margin-bottom: 1em;
    }
    @include from(m){
      font-size: 1.8em;
    }
    @include from(l){
      font-size: 2.5em;
    }
  }
}
.btn{
  display: table;
  font-size: 1.2em;
  line-height: 2em;
  padding: 0 1.2em;
  background: $color-second;
  text-transform: uppercase;
  margin: 0 auto;
  border-radius: .5em;
  transition: background .3s;
  &:hover{
    background: darken($color-second,10);
  }
  .mobile & {
    @include from(l) {
      font-size: 2em;
    }
  }
}
.social{
  @extend %list-style;
  --height: 1.5em;
  @include from(m){
    --height: 1.75em;
  }
  @include from(m){
    --height: 1.75em;
  }
  .mobile & {
    @include from(l) {
      --height: 2.75em;
    }
  }
  margin-top: calc(var(--height) * .75);
  display: flex;
  justify-content: center;
  align-items: center;
  li,a {
    display: block;
    width: var(--height);
    height: var(--height);
  }
  li{
    margin-right: calc(var(--height) / 2);
    &:last-child{
      margin-right: 0;
    }
    //background: lime;
    //&:nth-child(even){
    //  background: red;
    //}
  }
  a{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  svg{
    fill: $color-font;
    width: auto;
    height: 100%;
  }
}
@media screen and (orientation: landscape) {
  .mobile .container{
    transform-origin: left top;
    transform: scale(.75) translate(-50%, -50%);
    max-width: 75%;
  }
  .text{
    font-size: 1em;
  }
  .under{
    height: 1.5em !important;
    p{
      font-size: 1.1em !important;
    }
  }
}
